import React, { useRef, useEffect } from "react"

export default function Valine (props) {
  const defaultOptions = {
    appId: `KNdvK7kVT5sjgqgBISs7E2d4-MdYXbMMI`,
    appKey: `qrPYTPwNYncSTbPy7MT5mw9r`,
    avatar: `retro`,
    recordIP: false,
    serverURLs: `https://kndvk7kv.api.lncldglobal.com`,
  }

  const rootRef = useRef()

  useEffect(() => {
    if (rootRef.current) {
      import("valine").then(({ default: RealValine }) => {
        RealValine({
          el: rootRef.current,
          ...defaultOptions,
          ...props,
        })
      })
    }
  }, [rootRef, defaultOptions, props])

  return <div ref={rootRef}></div>
}
